import { product as productApi, productType as productTypeApi } from "../../api";
import actions from "./actions";

var add = function add(newCategory) {
  return actions.add(productApi.add(productApi.newId(), newCategory));
};

var remove = function remove(id) {
  return actions.remove(productApi.remove(id));
};

var edit = function edit(id, updates) {
  return actions.edit(productApi.edit(id, updates));
};

var getAll = function getAll() {
  return actions.getAll(productApi.getAll());
};

var listenChangesCallback = null;

var startListenChanges = function startListenChanges() {
  return function (dispatch) {
    if (!listenChangesCallback) {
      listenChangesCallback = function listenChangesCallback(products) {
        dispatch(actions.getAllFulfilled(products));
      };

      return productApi.getAll().then(function (products) {
        listenChangesCallback(products);
        productApi.startListenChanges(listenChangesCallback);
        return products;
      });
    }

    return Promise.reject("Listen alread started");
  };
};

var stopListenChanges = function stopListenChanges() {
  return function () {
    if (listenChangesCallback) {
      productApi.stopListenChanges(listenChangesCallback);
      listenChangesCallback = null;
    }
  };
};

export default {
  add: add,
  remove: remove,
  edit: edit,
  getAll: getAll,
  startListenChanges: startListenChanges,
  stopListenChanges: stopListenChanges
};