import { productType as productTypeApi } from "../../api";
import actions from "./actions";

var add = function add(newProductType) {
  return actions.add(productTypeApi.add(productTypeApi.newId(), newProductType));
};

var remove = function remove(id) {
  return actions.remove(productTypeApi.remove(id));
};

var edit = function edit(id, updates) {
  return actions.edit(productTypeApi.edit(id, updates));
};

var getAll = function getAll() {
  return actions.getAll(productTypeApi.getAll());
};

var listenChangesCallback = null;

var startListenChanges = function startListenChanges() {
  return function (dispatch) {
    if (!listenChangesCallback) {
      listenChangesCallback = function listenChangesCallback(productTypes) {
        dispatch(actions.getAllFulfilled(productTypes));
      };

      return productTypeApi.getAll().then(function (productTypes) {
        listenChangesCallback(productTypes);
        productTypeApi.startListenChanges(listenChangesCallback);
        return productTypes;
      });
    }

    return Promise.reject("Listen alread started");
  };
};

var stopListenChanges = function stopListenChanges() {
  return function () {
    if (listenChangesCallback) {
      productTypeApi.stopListenChanges(listenChangesCallback);
      listenChangesCallback = null;
    }
  };
};

export default {
  add: add,
  remove: remove,
  edit: edit,
  getAll: getAll,
  startListenChanges: startListenChanges,
  stopListenChanges: stopListenChanges
};