import { category as categoryApi } from "../../api";
import actions from "./actions";

var add = function add(newCategory) {
  return actions.add(categoryApi.add(categoryApi.newId(), newCategory));
};

var remove = function remove(id) {
  return actions.remove(categoryApi.remove(id));
};

var edit = function edit(id, updates) {
  return actions.edit(categoryApi.edit(id, updates));
};

var getAll = function getAll() {
  return actions.getAll(categoryApi.getAll());
};

var listenChangesCallback = null;

var startListenChanges = function startListenChanges() {
  return function (dispatch) {
    if (!listenChangesCallback) {
      listenChangesCallback = function listenChangesCallback(categories) {
        dispatch(actions.getAllFulfilled(categories));
      };

      return categoryApi.getAll().then(function (categories) {
        listenChangesCallback(categories);
        categoryApi.startListenChanges(listenChangesCallback);
        return categories;
      });
    }

    return Promise.reject("Listen alread started");
  };
};

var stopListenChanges = function stopListenChanges() {
  return function () {
    if (listenChangesCallback) {
      categoryApi.stopListenChanges(listenChangesCallback);
      listenChangesCallback = null;
    }
  };
};

export default {
  add: add,
  remove: remove,
  edit: edit,
  getAll: getAll,
  startListenChanges: startListenChanges,
  stopListenChanges: stopListenChanges
};